//component 
.OrderInfoCard
  background: var(--L1)
  border: 1px solid var(--border)
  border-radius: var(--br)
  padding: 15px 
  &:hover
    .secondaryHeading
      opacity: 1
  .row
    display: flex !important
    justify-content: space-between
    margin-bottom: 10px
  .heading
    margin: 0
    font-weight: 700
    font-size: .9rem
  .secondaryHeading
    font-size: .9rem
    color: var(--lowContrast)
    transition: opacity 200ms ease
    opacity: 0
  table tr
    line-height: 1.5
    font-size: .9rem
  table td 
    padding-right: 10px
    

.OrderLayout
  .Info
    width: 400px
    display: grid
    grid-template-rows: auto 100px
    height: 100%
    border-left: 1px solid var(--border)
    @media (max-width: 800px)
      max-width: 250px
    .wrap
      display: grid
      grid-template-columns: 1fr 
      grid-gap: 15px 10px
      padding: 1rem 1rem 
      height: fit-content !important
      position: relative
      .bold
        font-size: 1.2rem
        font-weight: 700
      .significantInfo
        display: grid
        grid-template-columns: 1fr 1fr 
        grid-template-rows: 40px auto
        grid-gap: 20px 10px
      .OrderComment
        grid-column: 1 / -1
        color: var(--highContrast)
        font-size: .9rem
        line-height: 1.5
        padding: 1rem 0
        white-space: pre-wrap
        pre
          padding-bottom: .5rem
   
    .actions
      position: fixed
      bottom: 0
      background: var(--L1)
      padding: 1em
      color: var(--highContrast)
      width: 100%
      font-size: .8rem
      border-top: 1px solid var(--border)

 
.paidVisualization
  td:first-of-type
    font-weight: 700 
    padding-right: 1em

